import React, { useContext, useState } from 'react';
import { isNil } from 'lodash';
import { formatEther } from 'ethers/lib/utils';
import logo from '../../../assets/images/logo.svg';
import { SelectWallet } from '../connect-wallet';
import { WalletContext } from '../../contexts/wallet-context';
import { BalanceContext } from '../../contexts/balance-context';
import './header.css';
import { DepositModal } from '../deposit-modal';
import { WithdrawModal } from '../withdraw-modal';

export const Header: React.FC = () => {
	const { account } = useContext(WalletContext);
	const [showConnectWallet, setShowConnectWallet] = useState(false);
	const [showDepositModal, setShowDepositModal] = useState(false);
	const [showWithdrawModal, setShowWithdrawModal] = useState(false);
	const { balance } = useContext(BalanceContext);

	return (
		<div className="flex flex-wrap w-full bg-[#333] px-4 py-3 items-center">
			<SelectWallet
				open={showConnectWallet}
				onClose={() => setShowConnectWallet(false)}
			/>
			<DepositModal open={showDepositModal} onClose={() => setShowDepositModal(false)} />
			<WithdrawModal open={showWithdrawModal} onClose={() => setShowWithdrawModal(false)} />
			<img src={logo} alt="" className="logo" />
			<div className="grow" />
			{isNil(account) ? (
				<button
					type="button"
					className="button"
					onClick={() => setShowConnectWallet(true)}
				>
					Connect Wallet
				</button>
			) : (
				<>
					<button
						type="button"
						className="button"
					>
						{account.slice(0, 6)}
						...
						{account.slice(account.length - 4, account.length)}
					</button>
					<button
						type="button"
						className="button ml-2"
						onClick={() => setShowDepositModal(true)}
					>
						Deposit
					</button>
					<button
						type="button"
						className="button ml-2"
						onClick={() => setShowWithdrawModal(true)}
					>
						Withdraw
					</button>
					{isNil(balance) ? null : (
						<div className="text-white text-lg ml-2">
							{formatEther(balance)}
							{' '}
							ETH
						</div>
					)}
				</>
			)}
		</div>
	);
};
