import React, {
	createContext,
	ReactNode,
	useMemo,
} from 'react';
import { isNil } from 'lodash';
import { useWeb3React } from '@web3-react/core';
import { Contract, ethers } from 'ethers';
import gameFacetAbi from '../../assets/contract-artifacts/game-facet-abi.json';

export namespace ContractContext {
	export type Value = {
		gameViewContract: Contract
		gameSignContract: Contract
	};
}

export const ContractContext = createContext<ContractContext.Value>({} as ContractContext.Value);
ContractContext.displayName = 'ContractContext';

export const ContractProvider = ({ children }: { children: ReactNode }) => {
	const { library } = useWeb3React();

	const gameViewContract = useMemo(
		() =>
			isNil(library)
				? null
				: new ethers.Contract(
					process.env.REACT_APP_CAMELS_CONTRACT_ADDRESS,
					gameFacetAbi,
					library
				),
		[library]
	);

	const gameSignContract = useMemo(
		() =>
			isNil(library)
				? null
				: new ethers.Contract(
					process.env.REACT_APP_CAMELS_CONTRACT_ADDRESS,
					gameFacetAbi,
					library.getSigner()
				),
		[library]
	);

	const value = useMemo<ContractContext.Value>(
		() => ({
			gameViewContract, gameSignContract,
		}), [gameViewContract, gameSignContract]
	);
	return <ContractContext.Provider value={value}>{children}</ContractContext.Provider>;
};
