import { toast } from 'react-toastify';

export const handleError = (error: Error) => {
	console.log(error.message);

	if (error.message.includes('insufficient funds')) {
		toast.error('Insufficient funds');
	} else if (error.message.includes('execution reverted: ')) {
		const messageParts = error.message.split('execution reverted: ');
		toast.error(messageParts[1].split('"')[0]);
	} else if (error.message.includes('reverted with reason string \'')) {
		const messageParts = error.message.split('reverted with reason string \'');
		toast.error(messageParts[1].split('\'')[0]);
	} else if (error.message.includes('user rejected transaction')) {
		toast.error('User rejected transaction');
	} else {
		toast.error('Unable to process transaction');
	}
};
