import React, { useContext, useState } from 'react';
import { useMutation } from '@tanstack/react-query';
import { toast } from 'react-toastify';
import { CircularProgress } from '@mui/material';
import { parseEther } from 'ethers/lib/utils';
import { Popup } from '../popup';
import { ContractContext } from '../../contexts/contract-context';
import './style.css';
import { handleError } from '../../utils/error.utils';
import { BalanceContext } from '../../contexts/balance-context';

interface Props {
  open: boolean;
  onClose: () => void
}

export const DepositModal = ({ onClose, open }: Props) => {
	const { reloadData } = useContext(BalanceContext);
	const { gameSignContract } = useContext(ContractContext);
	const [amount, setAmount] = useState('0');

	const { mutate, isLoading } = useMutation(async () => {
		const depositAmount = parseEther(amount);
		if (depositAmount.toBigInt() <= BigInt(0)) {
			toast.error('Amount must be greater than 0');
			return;
		}
		try {
			await gameSignContract.estimateGas.deposit({
				value: depositAmount,
			});
			const transaction = await gameSignContract.deposit({
				value: depositAmount,
			});
			await transaction.wait();

			reloadData();
			toast.success('Deposit successful');
			onClose();
		} catch (error) {
			handleError(error);
		}
	});

	return (
		<Popup
			open={open}
			onClose={onClose}
			className="bet-modal font-[Montserrat]"
		>
			<div className="flex flex-col text-white bg-camel-light rounded-[10px] items-center p-4 w-full md:w-[500px]">
				<div className="text-4xl font-extrabold">DEPOSIT</div>
				<input
					className="w-full rounded-md text-xl font-extrabold px-3 py-4 bg-camel mt-2 focus:outline-none"
					value={amount}
					onChange={(e) => setAmount(e.target.value)}
					type="number"
				/>
				<button
					type="button"
					className="w-full py-2 rounded-md bg-[#c9795a] my-2 font-extrabold disabled:opacity-50"
					onClick={() => mutate()}
					disabled={isLoading}
				>
					DEPOSIT
					{isLoading ? (
						<CircularProgress
							style={{
								height: '16px',
								width: '16px',
								color: 'black',
								marginLeft: '10px',
								marginBottom: '-3px',
							}}
						/>
					) : null}
				</button>
				<button
					type="button"
					className="w-full py-2 rounded-md bg-camel font-extrabold disabled:opacity-50"
					onClick={onClose}
					disabled={isLoading}
				>
					CANCEL
				</button>
			</div>
		</Popup>
	);
};
