import React, { useContext, useMemo, useState } from 'react';
import { TwitchEmbed } from 'react-twitch-embed';
import { isNil, padStart } from 'lodash';
import { useQuery } from '@tanstack/react-query';
import Countdown from 'react-countdown';
import { Header } from '../components/header/header';
import { PlaceBet } from '../components/place-bet/place-bet';
import { BetModal } from '../components/bet-modal';
import redCamel from '../../assets/images/red_camel.png';
import blueCamel from '../../assets/images/blue_camel.png';
import greenCamel from '../../assets/images/green_camel.png';
import orangeCamel from '../../assets/images/orange_camel.png';
import { ContractContext } from '../contexts/contract-context';
import { Bet } from '../types/bet';
import { generateNonce } from '../utils/nonce.utils';

export const Home: React.FC = () => {
	const [selectedCamel, setSelectedCamel] = useState<number | null>(null);
	const { gameViewContract } = useContext(ContractContext);

	const { data: gameId, isLoading: isGameIdLoading } = useQuery(['gameId', isNil(gameViewContract)], async () => {
		if (gameViewContract) {
			const [gameId] = await gameViewContract.getLatestGameId(generateNonce());
			return gameId;
		}
		return null;
	});

	const { data: game, isLoading: isGameLoading } = useQuery(['game', gameId], async () => {
		const [game] = await gameViewContract.getGame(gameId, generateNonce());
		console.log(game);
		return game;
	}, { enabled: !isNil(gameId) });

	const bets = useMemo(() => {
		const bets = {
			1: [] as Bet[],
			2: [] as Bet[],
			3: [] as Bet[],
			4: [] as Bet[],
		};
		if (isNil(game)) return bets;
		for (let i = 0; i < game.players.length; i++) {
			const camelId = game.bets[i].toNumber();
			const amount = game.betAmounts[i];
			const player = game.players[i];
			bets[camelId].push({
				player,
				amount,
			});
		}
		return bets;
	}, [game]);

	return (
		<div className="flex flex-col font-[Montserrat] h-screen">
			<BetModal
				open={!isNil(selectedCamel)}
				onClose={() => setSelectedCamel(null)}
				camelId={selectedCamel}
				gameId={gameId}
				minBet={game?.minBet}
			/>
			<Header />
			<div className="flex flex-1 p-[20px] min-h-[600px]">
				{/* <div className="w-[185px] mr-[10px]">
					<Results />
				</div> */}
				<div className="w-full">
					<TwitchEmbed
						channel="camelsgg"
						autoplay
						withChat={false}
						width="100%"
						height="100%"
					/>
				</div>
			</div>
			{game?.started ? (
				<div className="flex flex-col w-full">
					<div className="text-white text-8xl self-center my-3 animate-pulse">
						RACING
					</div>
				</div>
			) : (
				<div className="flex flex-col w-full">

					<div className="flex rounded-lg bg-camel text-white text-lg p-[7px] mx-4 w-fit self-center mt-3">
						<div className="mr-1">Place your bets, race starts in</div>
						{isNil(game)
							? null
							: (
								<Countdown
									date={game.beginTimestamp.toNumber() * 1000}
									renderer={({ hours, minutes, seconds }) => (
										<span>
											{padStart(hours.toString(), 2, '0')}
											:
											{padStart(minutes.toString(), 2, '0')}
											:
											{padStart(seconds.toString(), 2, '0')}
										</span>
									)}
								/>
							)}
					</div>
					<div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 text-white p-6 gap-4">
						<PlaceBet
							id={1}
							image={blueCamel}
							bets={bets[1]}
							onPlaceBet={() => setSelectedCamel(1)}
						/>
						<PlaceBet
							id={2}
							image={redCamel}
							bets={bets[2]}
							onPlaceBet={() => setSelectedCamel(2)}
						/>
						<PlaceBet
							id={3}
							image={greenCamel}
							bets={bets[3]}
							onPlaceBet={() => setSelectedCamel(3)}
						/>
						<PlaceBet
							id={4}
							image={orangeCamel}
							bets={bets[4]}
							onPlaceBet={() => setSelectedCamel(4)}
						/>
					</div>
				</div>
			)}
		</div>
	);
};
