import React from 'react';
import { formatEther } from 'ethers/lib/utils';
import { camels } from '../../types/camels';
import { Bet } from '../../types/bet';

type Props = {
  id: number
  image: string
  bets: Bet[]
  onPlaceBet: () => void
};

export const PlaceBet: React.FC<Props> = ({ id, image, bets, onPlaceBet }) => {
	return (
		<div className="flex flex-col">
			<div className="text-2xl self-center font-extrabold">{camels[id]}</div>
			<img className="h-[60px] w-[60px] self-center mb-2" src={image} alt="" />
			<button
				type="button"
				className="rounded-lg font-extrabold bg-[#c9795a] py-1 w-full max-w-[150px] self-center"
				onClick={onPlaceBet}
			>
				PLACE BET
			</button>
			<div className="flex flex-col rounded-lg bg-camel mt-2 p-2 font-[RobotoMono] min-h-[100px]">
				{bets.map((bet) => (
					<div className="flex justify-between" key={bet.player}>
						<div>
							{bet.player.slice(0, 6)}
							...
							{bet.player.slice(bet.player.length - 4, bet.player.length)}
						</div>
						<div>
							{formatEther(bet.amount)}
							{' '}
							ETH
						</div>
					</div>
				))}
			</div>
		</div>
	);
};
