import React, { useContext, useState } from 'react';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { toast } from 'react-toastify';
import { isNil } from 'lodash';
import { CircularProgress } from '@mui/material';
import { parseEther } from 'ethers/lib/utils';
import { Popup } from '../popup';
import { camels } from '../../types/camels';
import { ContractContext } from '../../contexts/contract-context';
import './style.css';
import { handleError } from '../../utils/error.utils';
import { BalanceContext } from '../../contexts/balance-context';

interface Props {
  open: boolean;
  onClose: () => void
  gameId: number
  camelId: number
  minBet: bigint
}

export const BetModal = ({ onClose, open, gameId, camelId, minBet }: Props) => {
	const queryClient = useQueryClient();
	const { balance, reloadData } = useContext(BalanceContext);
	const { gameSignContract } = useContext(ContractContext);
	const [amount, setAmount] = useState('0');

	const { mutate, isLoading } = useMutation(async () => {
		if (isNil(gameId)) {
			toast.error('Invalid game');
			return;
		}
		const betAmount = parseEther(amount);
		if (betAmount.toBigInt() <= BigInt(0) || betAmount.toBigInt() < minBet) {
			toast.error(`Minimum bet is ${minBet}`);
			return;
		}
		try {
			let transaction = null;

			if (balance >= betAmount.toBigInt()) {
				await gameSignContract.estimateGas.placeBet(gameId, camelId, betAmount);
				transaction = await gameSignContract.placeBet(gameId, camelId, betAmount);
			} else {
				await gameSignContract.estimateGas.depositAndPlaceBet(gameId, camelId, betAmount, {
					value: betAmount,
				});
				transaction = await gameSignContract.depositAndPlaceBet(gameId, camelId, betAmount, {
					value: betAmount,
				});
			}

			await transaction.wait();

			queryClient.invalidateQueries(['game']);
			reloadData();
			toast.success('Bet placed successfully');
			onClose();
		} catch (error) {
			handleError(error);
		}
	});

	return (
		<Popup
			open={open}
			onClose={onClose}
			className="bet-modal font-[Montserrat]"
		>
			<div className="flex flex-col text-white bg-camel-light rounded-[10px] items-center p-4 w-full md:w-[500px]">
				<div className="text-4xl font-extrabold">NEW BET</div>
				<div className="text-2xl font-extrabold italic">{camels[camelId]}</div>
				<input
					className="w-full rounded-md text-xl font-extrabold px-3 py-4 bg-camel mt-2 focus:outline-none"
					value={amount}
					onChange={(e) => setAmount(e.target.value)}
					type="number"
				/>
				<button
					type="button"
					className="w-full py-2 rounded-md bg-[#c9795a] my-2 font-extrabold disabled:opacity-50"
					onClick={() => mutate()}
					disabled={isLoading}
				>
					PLACE BET
					{isLoading ? (
						<CircularProgress
							style={{
								height: '16px',
								width: '16px',
								color: 'black',
								marginLeft: '10px',
								marginBottom: '-3px',
							}}
						/>
					) : null}
				</button>
				<button
					type="button"
					className="w-full py-2 rounded-md bg-camel font-extrabold disabled:opacity-50"
					onClick={onClose}
					disabled={isLoading}
				>
					CANCEL
				</button>
				<div className="text-[#c9795a] mt-2 font-extrabold">
					The return on your bet can change based on other camels wagers, and it
					may be adjusted up to 1 minute before the race begins.
				</div>
			</div>
		</Popup>
	);
};
