import React, {
	createContext,
	ReactNode,
	useContext,
	useEffect,
	useMemo,
	useState,
} from 'react';
import { useWeb3React } from '@web3-react/core';
import { ContractContext } from './contract-context';
import { generateNonce } from '../utils/nonce.utils';

export namespace BalanceContext {
	export type Value = {
		balance: bigint
        reloadData: () => void
	};
}

export const BalanceContext = createContext<BalanceContext.Value>({} as BalanceContext.Value);
BalanceContext.displayName = 'BalanceContext';

export const BalanceProvider = ({ children }: { children: ReactNode }) => {
	const { account } = useWeb3React();
	const { gameViewContract } = useContext(ContractContext);
	const [balance, setBalance] = useState<bigint | null>(null);

	const reloadData = useMemo(() => () => {
		if (gameViewContract) {
			gameViewContract
				.batchGetBalances([account], generateNonce())
				.then(([balances]) => setBalance(balances[0]));
		}
	}, [account, gameViewContract]);

	useEffect(() => {
		reloadData();
	}, [reloadData]);

	const value = useMemo<BalanceContext.Value>(
		() => (
			{
				balance,
				reloadData,
			}),
		[balance, reloadData]
	);
	return <BalanceContext.Provider value={value}>{children}</BalanceContext.Provider>;
};
